<template>
    <div>
        <div class="page-container">
            <div class="page-section">
                <DxDataGrid
                id="data-grid-list"
                key-expr="id"
                :ref="gridRefName"
                :data-source="tagRegistrationList"
                :hover-state-enabled="true"
                :allow-column-reordering="true"
                :show-borders="true"
                :show-row-lines="true"
                :row-alternation-enabled="false"
                :column-hiding-enabled="false"
                :word-wrap-enabled="true"
                :column-min-width="100"
                :column-auto-width="true"
                >
                <DxEditing
                    :allow-updating="false"
                    :allow-deleting="false"
                    :allow-adding="false"
                    :use-icons="true"
                    mode="form"
                >
                    
                </DxEditing>
                <DxFilterRow :visible="true" />
                <DxHeaderFilter :visible="true" />

                <DxColumn 
                    data-field="id_platform"
                    caption="Platform" 
                    alignment="center" 
                    :width="50"
                >
                    <DxLookup :data-source="platformList" display-expr="code_name" value-expr="id" />
                </DxColumn>
                <DxColumn
                    data-field="tag_no"
                    caption="Tag No." 
                    alignment="left"
                    :min-width="150"
                />
                <DxColumn 
                    data-field="desc" 
                    caption="Description" 
                    alignment="left"  
                    :min-width="150"
                >
                </DxColumn>
                <DxColumn 
                    data-field="is_pigging_opt" 
                    caption="Pigging Operation" 
                    alignment="center"
                    :width="120"
                    :visible="isPipeline"
                />
                <DxColumn 
                    data-field="is_water_analysis" 
                    caption="Water Analysis" 
                    alignment="center"
                    :width="120"
                />
                <DxColumn 
                    data-field="is_micro_bacteria" 
                    caption="Microbilogical Bacteria"
                    alignment="center" 
                    :width="120"
                />
                <DxColumn 
                    data-field="is_corrosion_coupon" 
                    caption=" Corrosion Coupon" 
                    alignment="center" 
                    :width="120" 
                />
                <DxColumn 
                    data-field="is_er_probe" 
                    caption="ER Probe" 
                    alignment="center" 
                    :width="120"
                />
                <DxColumn 
                    data-field="is_ci" 
                    caption="Chemical Injection" 
                    alignment="center"
                    :width="120" 
                    :visible="isPipeline"
                />
                <DxColumn 
                    data-field="is_rci" 
                    caption="Residual Corrosion Inhibitor" 
                    alignment="center"
                    :width="120" 
                    :visible="isPipeline"
                />
                <DxColumn :width="80" alignment="center" cell-template="action-cell-template" />
        
                <template #action-cell-template="{ data }">
                    <div class="action-wrapper">
                    <div @click="() => [isShow = 2, selectedData = data.data]">
                        <img src="/img/svg/pen-svg.svg" class="penSvg" />
                    </div>
                    <div @click="DELETE_TAG(data)">
                        <img src="/img/svg/trash-svg.svg" class="trashSvg" />
                    </div>
                    </div>
                </template>
                <DxToolbar>
                    <DxItem
                        location="after"
                        template="addButton"
                    />
                    <DxItem
                        location="after"
                        name="searchPanel"
                    />
                </DxToolbar>
                <template #addButton>
                    <DxButton
                    icon="las la-plus"
                    @click="ADD_ROW"
                    hint="Add"
                    />
                </template>
                
                
                <!-- <DxFilterRow :visible="true" /> -->
                <DxScrolling mode="standard" />
                <DxSearchPanel :visible="true" />
                <DxPaging :page-size="10" :page-index="0" />
                <DxPager 
                    :show-page-size-selector="true" 
                    :allowed-page-sizes="[5, 10, 20]" 
                    :show-navigation-buttons="true"
                    :show-info="true" 
                    info-text="Page {0} of {1} ({2} items)" 
                />
                <DxExport :enabled="false" />
                </DxDataGrid>
            </div>
        </div>
        <AddTagRegistration 
            v-if="isShow === 1" 
            :system="system"
            @popup="FETCH_DATA('/CMInfo', 'tagRegistrationList')" 
        />
        <EditTagRegistration 
            v-if="isShow === 2" 
            :system="system"
            :infoData="selectedData" 
            @popup="FETCH_DATA('/CMInfo', 'tagRegistrationList')" 
        />
    </div>
</template> 

<script>
import { axios } from "/axios.js";
import AddTagRegistration from "./Add.vue"
import EditTagRegistration from "./Edit.vue"
import "devextreme/dist/css/dx.light.css";
import { Workbook } from "exceljs";
import saveAs from "file-saver";
import { exportDataGrid } from "devextreme/excel_exporter";
import { DxItem } from "devextreme-vue/form";
import DxButton from "devextreme-vue/button";

import {
    DxDataGrid,
    DxSearchPanel,
    DxPaging,
    DxPager,
    DxScrolling,
    DxColumn,
    DxExport,
    DxToolbar,
    DxEditing,
    DxLookup,
    DxFilterRow,
    DxHeaderFilter,
    // DxButton,
    // DxFormItem,
    // DxForm
} from "devextreme-vue/data-grid";

export default {
    name: "tag-registration",
    components: {
        DxDataGrid,
        DxSearchPanel,
        DxPaging,
        DxPager,
        DxScrolling,
        DxColumn,
        DxExport,
        DxToolbar,
        // DxForm,
        DxItem,
        DxEditing,
        DxLookup,
        DxFilterRow,
        DxHeaderFilter,
        DxButton,
        // DXButton,
        // DxFormItem,
        // penSvg,
        // trashSvg,
        AddTagRegistration,
        EditTagRegistration
    },
    created() {
        this.$store.commit("UPDATE_CURRENT_PAGENAME", {
            subpageName: "TAG REGISTRATION",
            subpageInnerName: null,
        });
        if (this.$store.state.status.server == true) {
            this.tagRegistrationList = [];
            this.system = this.$route.fullPath.split('/')[2];
            this.isCoolingMedium = this.CHECK_SYSTEM('cooling-medium');
            this.isProducedWater = this.CHECK_SYSTEM('produced-water');
            this.isSeaWater = this.CHECK_SYSTEM('sea-water');
            this.isPipeline = this.CHECK_SYSTEM('pipeline');
            this.FETCH_DATA('/Md/get-md-platform-list', 'platformList');
            this.FETCH_DATA('/CMInfo', 'tagRegistrationList');
        }
    },
    data() {
        return {
            platformList: [],
            tagRegistrationList: [],
            isShow: 0,
            selectedId: null,
            gridRefName: "grid",
            system: null,
            isCoolingMedium: false,
            isProducedWater: false,
            isSeaWater: false,
            isPipeline: false,
        };
    },
    computed: {
        baseURL() {
            var mode = this.$store.state.mode;
            if (mode == "dev") return this.$store.state.modeURL.dev;
            else if (mode == "prod") return this.$store.state.modeURL.prod;
            else return console.log("develpment mode set up incorrect.");
        },
        SYSTEM_ID() {
            if(this.system == 'cooling-medium') return 1; 
            else if(this.system == 'produced-water') return 2; 
            else if(this.system == 'sea-water') return 3; 
            else if(this.system == 'pipeline') return 4;
            else return 0;
        },
    },
    methods: {
        EXPORT_DATA(e) {
            const workbook = new Workbook();
            const worksheet = workbook.addWorksheet("Projects");
            exportDataGrid({
                worksheet: worksheet,
                component: e.component
            }).then(function () {
                workbook.xlsx.writeBuffer().then(function (buffer) {
                    saveAs(
                        new Blob([buffer], { type: "application/octet-stream" }),
                        "CM-TAG-REGISTATION.xlsx"
                    );
                });
            });
            e.cancel = true;
        },
        CHECK_SYSTEM(e) {
            if (this.system === e) return true;
            else return false;
        },
        ADD_ROW() {
            this.isShow = 1
        },
        DELETE_TAG(e) {
            console.log(e);
            this.$ons.notification.confirm("Confirm Delete This Tag Number?").then((res) => {
                if (res == 1) {
                    axios({
                        method: "delete",
                        url: "/CMInfo/" + e.key,
                        headers: {
                            Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
                        },
                    })
                        .then(res => {
                            console.log('DELETE_TAG', res);
                            if (res.status == 204) {
                                this.FETCH_DATA('/CMInfo', 'tagRegistrationList');
                            }
                        })
                        .catch(error => {
                            this.$ons.notification.alert(
                                error.code + " " + error.response.status + " " + error.message
                            );
                        })
                        .finally(() => {
                            this.isLoading = false;
                        });
                }
            })
        },
        FETCH_DATA(url, targetVariable, callback) {
            this.isLoading = true;
            axios({
                method: "get",
                url: url,
                headers: {
                    Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
                }
            })
                .then(res => {
                    if (res.status == 200 && res.data) {
                        if (callback && typeof callback === 'function') {
                            callback(res.data);
                        } else {
                            this.$set(this, targetVariable, res.data);
                            if(url == '/CMInfo') {
                                this.isShow = 0;
                                this.tagRegistrationList = this.tagRegistrationList.filter(t => t.id_system == this.SYSTEM_ID);
                            }
                        }
                    }
                })
                .catch(error => {
                    console.log(error);
                })
                .finally(() => {
                    this.isLoading = false;
                });
        },
    }
};
</script>

